<template>
  <div id="app">
    <v-app>
      <v-main>
        <div style="display:flex; width:100%" v-if="ready">
          <vue-tabs-chrome theme="custom" ref="tab" v-model="active_tab" :tabs="tabs" :gap="0" :autoHiddenCloseIconWidth="170"
            :onClose="CloseTab" :tabCloseWidth="50">
            <button slot="after" @click="AddTab" class="add-button fluent-icon"> &#xE710;</button>
            <template #progress="{ tab}">
              <v-progress-linear  :value="(tab.character.done.length / tab.character.routines.length) * 100 " :color="active_tab  == tab.character.nickname ? 'neon' : 'grey'"></v-progress-linear>
            </template>
          </vue-tabs-chrome>
        </div>
      </v-main>
    </v-app>

  </div>
</template>
<script>
// import VueTabsChrome from 'vue-tabs-chrome'
import VueTabsChrome from './packages'
import "@/assets/style.scss"
const procedure = async (data) => {
  try {
    await window.chrome.webview.postMessage(data)
  }
  catch (ex) {
    console.log(ex)
  }
}
export default {
  components: {
    VueTabsChrome
  },
  data: () => ({
    active_tab: "",
    tabs: [],
    ready: false,
    workspace_mode: new URL(window.location).searchParams.get("workspace_mode"),
    progress: 20


  }),
  watch: {
    tab(new_key, old_key) {
      let tab = this.tabs.find((tab) => tab.character.nickname == new_key)
      if (tab !== undefined && old_key !== new_key) procedure({ Event: 'focus_tab', Data: tab.character.nickname })
    },
  },
  methods: {
    CloseTab(e) {
      procedure({ Event: 'close_tab', Data: e.character.nickname })
      console.log("CloseCharacter", e.character, e)
      return true
    },
    CloseTabByRemote(nickname) {
      this.$refs.tab.removeTab(nickname)
    },
    AddTab() {
      if (this.workspace_mode == "freemode") procedure({ Event: 'select_character' })
      else if (this.workspace_mode == "automate") procedure({ Event: "fetch_character" })
    },
    SyncTabs(tabs_cfg) {
      try {
        console.log(tabs_cfg)
        var tabs = JSON.parse(tabs_cfg).map((character) => ({
          label: character.nickname,
          key: character.nickname,
          character,
          dragable: false,
          favicon: () => { }
        }))

        console.log(tabs)
        this.tabs = tabs

        console.log(this.tabs)

      } catch (ex) {
        console.log(ex)
      } finally {
        setTimeout(() => {
          this.ready = true
        }, 300)
      }

    },

    SetActiveTab(active_tab) {
      this.active_tab = active_tab
      console.log(this.active_tab)
    },

    LoadTab(character) {
      let tab = this.tabs.find((tab) => tab.character.nickname == character.nickname)
      console.log("mounting", tab, tab == undefined, character)
      if (tab == undefined) {
        if (this.$refs.tab) {
          this.$refs.tab.addTab({
            label: character.nickname,
            key: character.nickname,
            character,
            dragable: false,
            favicon: () => { }
          })
        }
        if (this.tabs.length == 1 || this.workspace_mode == 'freemode') {
          setTimeout(() => {
            this.active_tab = character.nickname
          }, 100)
        }
      }
    },
    ChangeProgress({nickname, done}){
      let tab = this.tabs.find((tab) => tab.character.nickname == nickname)
      if (tab) {
        tab.character.done = done
      }
    }
    
  },
  mounted() {
    window.$com = this
    this.ready = true
    // setTimeout(() => {
    //   this.LoadTab({ nickname: "Arika", progress : 20 })
    //   this.LoadTab({ nickname: "Arka", progress : 50 })
    // }, 200)

  }
}
</script>
